.reviews {
  padding-top: 115px;
  padding-bottom: 64px;

  @media screen and (max-width: @small) {
    padding-top: 40px;
    padding-bottom: 50px;
  }
}

.reviews__title-wrapper {
  width: 100%;
  margin-bottom: 60px;

  @media screen and (max-width: @small) {
    margin-bottom: 30px;
  }
}

.reviews__slider {
  width: 100%;
}

.rewiews__slide-img {
  border-radius: 50%;
  display: block;
  margin: 0 auto;
  margin-bottom: 12px;
}

.rewiews__name {
  display: block;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  line-height: 25px;
}

.rewiews__profession {
  text-align: center;
  opacity: 0.5;
  margin-bottom: 24px;
}

.reviews__txt {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
}
