.staff {
  padding-top: 80px;
  padding-bottom: 42px;
}

.staff__list {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;

  @media screen and (max-width: @small) {
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
  }
}

.staff__list-item {
  width: 30%;
  margin-bottom: 62px;

  &:nth-child(3n - 1) {
    margin-right: auto;
    margin-left: auto;
  }

  @media screen and (max-width: @small) {
    width: 100%;
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    padding: 0 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.staff__profession {
  display: block;
  font-size: 18px;
  font-weight: bold;
  line-height: 27px;
  margin-top: 20px;
  margin-bottom: 8px;
}

.staff__name {
  font-size: 20px;
  line-height: 30px;
}

.staff__status {
  margin-bottom: 16px;
}

.staff__dscr {
  height: 84px;
  overflow-y: hidden;
  margin-bottom: 16px;
  opacity: 0.6;
}

.staff__dscr--active {
  height: auto;
  opacity: 1;
}

.staff__more {
  background-color: transparent;
  color: @blue;
  border: none;
  position: relative;
  padding-right: 40px;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  .staff__icon {
    width: 30px;
    height: 11px;
    position: absolute;
    top: 4px;
    right: 0;
  }
}
