.why-we {
  background-color: @light-blue;
  background-image: url("../img/form-bg.jpg");
  background-size: 50% auto;
  background-position: 100% 0;
  background-repeat: no-repeat;

  @media screen and (max-width: @large) {
    background-position: 50% 100%;
    background-size: 100% 50%;
  }

  @media screen and (max-width: @small) {
    background-size: 100% 45%;
  }
}

.why-we__wrapper {
  padding-top: 90px;
  padding-bottom: 78px;
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @large) {
    flex-direction: column;
  }

  @media screen and (max-width: @small) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.why-we__info {
  width: 100%;
}

.why-we__title {
  display: block;
  font-size: 32px;
  line-height: 39px;
  color: @blue;
  margin-top: 0;
  margin-bottom: 40px;

  @media screen and (max-width: @small) {
    text-align: center;
  }
}

.why-we__list {
  padding-right: 76px;

  @media screen and (max-width: @small) {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.why-we__list-item {
  min-height: 80px;
  margin-bottom: 22px;
  padding-left: 92px;
  position: relative;

  @media screen and (max-width: @small) {
    padding-left: 52px;
  }
}

.why-we__icon-wrapper {
  width: 80px;
  height: 80px;
  position: absolute;
  border: 10px solid #e0e7f3;
  border-radius: 50%;
  top: 0;
  left: 0;
  background: linear-gradient(87.71deg, #18449E 0%, #2D65BC 100%);

  @media screen and (max-width: @small) {
    width: 40px;
    height: 40px;
    border: 2px solid #e0e7f3;
  }

  .why-we__icon {
    width: 24px;
    height: 24px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    fill: @white;
  }
}

.why-we__list-bold-txt {
  display: block;
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 8px;
}

.why-we__form-wrapper {
  width: 100%;
  padding-top: 40px;
}

.why-we__form {
  width: 340px;
  margin-left: auto;

  @media screen and (max-width: @large) {
    margin-right: auto;
  }

  @media screen and (max-width: @small) {
    width: 320px;
    padding: 20px;
  }
}

.why-we__form-title {
  display: block;
  font-size: 32px;
  line-height: 39px;
  color: @white;
  margin-bottom: 8px;

  @media screen and (max-width: @small) {
    font-size: 25px;
    line-height: 25px;
  }
}

.why-we__form-txt {
  color: @white;
  margin-bottom: 32px;
}

.why-we__input {
  width: 100%;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 20px;
  padding-bottom: 19px;
  margin-bottom: 16px;
  border-radius: 4px;
  border: none;

  &::placeholder {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: @black;
    opacity: 0.5;
  }

  @media screen and (max-width: @small) {
    padding: 12px;
  }
}

.why-we__textarea {
  width: 100%;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 20px;
  padding-bottom: 19px;
  margin-bottom: 16px;
  border-radius: 4px;
  border: none;
  min-height: 142px;

  &::placeholder {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: @black;
    opacity: 0.5;
  }

  @media screen and (max-width: @small) {
    min-height: 88px;
  }
}

.why-we__btn {
  display: inline-block;
  padding: 5px 16px;
  background-color: #e30613;
  color: @white;
  font-weight: 700;
  font-size: 12px;
  line-height: 21px;
  border: none;
  border-radius: 38px;
  cursor: pointer;
  transition: all ease-in-out 0.2s;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  margin-top: 30px;

  li {
    width: 25px;
    height: 25px;
    margin: 0 5px;

    button {
      display: block;
      height: 6px;
      width: 6px;
      background-color: @blue;
      font-size: 0;
      border: none;
      padding: 0;
      border-radius: 3px;
      opacity: 0.6;
      margin: 0 auto;
    }
  }

  .slick-active {
    button {
      width: 22px;
      opacity: 1;
      margin: 0;
    }
  }
}

.slick-next.slick-arrow {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0;
  width: 40px;
  height: 28px;
  background-image: url("../img/sprite.svg#right-arrow-icon");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 55% 50%;
  border: none;
  background-color: transparent;
  z-index: 10;
  cursor: pointer;
  outline: none;
  transition: all ease-in 0.2s;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.slick-prev.slick-arrow {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0;
  width: 40px;
  height: 28px;
  background-image: url("../img/sprite.svg#left-arrow-icon");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 45% 50%;
  border: none;
  background-color: transparent;
  z-index: 10;
  cursor: pointer;
  outline: none;
  transition: all ease-in 0.2s;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}
