.laboratory-info {
  padding-top: 80px;
  padding-bottom: 100px;

  @media screen and (max-width: @small) {
    padding-top: 40px;
    padding-bottom: 30px;
  }
}

.laboratory__title-wrapper {
  margin-bottom: 30px;
}

.laboratory-info__list {
  margin: 0 auto;

  @media screen and (max-width: @small) {
    width: 300px;
  }
}

.laboratory-info__list-item {
  padding-left: 12px;
  position: relative;
  font-size: 14px;
  line-height: 28px;
  margin-bottom: 10px;

  @media screen and (max-width: @small) {
    padding-left: 15px;
  }

  &::before {
    content: "";
    display: block;
    width: 8px;
    height: 8px;
    background-color: @blue;
    position: absolute;
    top: 50%;
    left: 0;
    border-radius: 50%;
    transform: translateY(-50%);
  }
}
