.prices {
  background-image: url("../img/prices.jpg");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
}

.prices__start-screen {
  padding-top: 132px;
  padding-bottom: 132px;

  @media screen and (max-width: @small) {
    padding-top: 68px;
    padding-bottom: 68px;
  }
}

.prices__title {
  font-size: 32px;
  color: @white;
  line-height: 39px;
  text-align: center;
}
