.map {
  padding-top: 80px;
  padding-bottom: 80px;
  background-image: url("../img/map-bg.jpg");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
}

.map__info {
  width: 277px;
  height: 140px;
  box-shadow: 8px 8px 20px rgba(0, 0, 0, 0.12);
  border-radius: 14px;
  background-color: @white;
  position: relative;
  margin: 0 auto;
  padding: 32px;

  &::after {
    content: "";
    display: block;
    position: absolute;
    border: 12px solid transparent;
    border-top-color: @white;
    bottom: -24px;
    right: 25px;
  }
}

.map__info-logo {
  margin-bottom: 10px;
}

.map__link {
  display: block;
  padding-right: 30px;
  position: relative;

  .map__icon {
    width: 30px;
    height: 11px;
    position: absolute;
    top: 4px;
    right: 0;
  }
}
