.footer {
  background-color: @blue;
}

.footer__wrapper {
  max-width: 1440px;
  padding: 18px 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;

  @media screen and (max-width: @small) {
    padding: 18px 20px;
    flex-direction: column;
  }
}


.footer__soc-list {
  display: flex;
  justify-content: space-between;
  width: 180px;

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.footer__soc-list-item {
  width: 26px;
  height: 20px;
}

.footer__soc-list-link {
  display: block;
  position: relative;
}

.footer__soc-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 26px;
  height: 20px;
  transition: all ease-in 0.2s;
  fill: @white;

  &:hover {
    fill: @red;
  }
}

.footer__txt {
  color: @white;
}
