.pop-up {
  display: none;
  width: 460px;
  padding: 60px;
  background-color: @light-blue;
  border-radius: 4px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 11;
  border: 1px solid @black;

  @media screen and (max-width: @small) {
    width: 300px;
    padding: 18px;
  }
}

.pop-up__btn-close {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 40px;
  right: 40px;
  padding: 0;
  border: 0;
  background-color: transparent;

  @media screen and (max-width: @small) {
    top: 18px;
    right: 18px;
  }
}

.pop-up__close-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
}

.pop-up__title {
  display: block;
  font-size: 32px;
  line-height: 39px;
  color: @black;
  margin-bottom: 8px;

  @media screen and (max-width: @small) {
    font-size: 18px;
    line-height: 25px;
  }
}

.pop-up__form-txt {
  color: @black;
  margin-bottom: 32px;
}

.pop-up__input {
  width: 100%;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 20px;
  padding-bottom: 19px;
  margin-bottom: 16px;
  border-radius: 4px;
  border: none;

  &::placeholder {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: @black;
    opacity: 0.5;
  }

  @media screen and (max-width: @small) {
    padding: 12px;
  }
}

.pop-up__textarea {
  width: 100%;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 20px;
  padding-bottom: 19px;
  margin-bottom: 24px;
  border-radius: 4px;
  border: none;
  min-height: 142px;

  &::placeholder {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: @black;
    opacity: 0.5;
  }

  @media screen and (max-width: @small) {
    min-height: 88px;
  }
}

.pop-up__btn {
  width: 100%;
  line-height: 21px;
  display: block;
  padding: 5px 16px;
  background-color: @red;
  color: @white;
  font-weight: bold;
  font-size: 12px;
  border: none;
  border-radius: 38px;
  cursor: pointer;
  transition: all ease-in-out 0.2s;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}
