.btn {
  display: inline-block;
  padding: 5px 16px;
  background-color: @red;
  color: @white;
  font-weight: bold;
  font-size: 12px;
  line-height: 21px;
  border: none;
  border-radius: 38px;
  cursor: pointer;
  transition: all ease-in-out 0.2s;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}
