.prices-info {
  padding-top: 80px;
  padding-bottom: 100px;

  @media screen and (max-width: @small) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.prices__title-wrapper {
  margin-bottom: 30px;
}

.prices-info__list-buttons {
  @media screen and (max-width: @small) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 4px;
    padding-right: 4px;
    margin-bottom: 20px;
  }
}

.prices-info__doctor-btn {
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  transition: all ease-in 0.2s;

  &:hover {
    color: @blue;
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  @media screen and (max-width: @small) {
    font-weight: 400;
    border: 1px solid @blue;
    border-radius: 4px;
    margin-bottom: 5px;
  }
}

.prices-info__doctor-btn--active {
  color: @blue;

  @media screen and (max-width: @small) {
    color: @white!important;
    background-color: @blue;
  }
}

.prices-info__list {
  width: 100%;
  max-width: 760px;
  margin-left: auto;

  @media screen and (max-width: @large) {
    max-width: 560px;
  }
}

.prices-info__list-item {
  display: none;
}

.prices-info__list-item--active {
  display: block;
}

.prices-info__doctor-services-list {
  .prices-info__doctor-services-list-item {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    line-height: 28px;
    padding-top: 6px;
    padding-bottom: 4px;
    padding-left: 10px;
    padding-right: 10px;

    &:nth-child(2n - 1) {
      background-color: @light-blue;
    }
  }
}


