.title-shadow {
  position: relative;
  margin-bottom: 40px;

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.title-shadow__title-text {
  font-size: 32px;
  line-height: 39px;
  color: @blue;
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;

  @media screen and (max-width: @small) {
    padding-left: 10px;
  }
}

.title-shadow__shadow-text {
  font-size: 92px;
  font-weight: bold;
  line-height: 1;
  color: @light-blue;

  @media screen and (max-width: @large) {
    font-size: 82px;
  }

  @media screen and (max-width: @small) {
    font-size: 57px;
    padding-left: 30px;
  }
}

.title-shadow--center {
  width: 100%;

  .title-shadow__title-text {
    left: 50%;
    top: 10px;
    transform: translateX(-50%);
    z-index: 10;
  }

  .title-shadow__shadow-text {
    text-align: center;
  }
}
