.start-screen {
  background-image: url("../img/main-bg-1.jpg");
  background-size: 100% 648px;
  background-repeat: no-repeat;
  background-position: 0 0;

  @media screen and (max-width: @large) {
    background-size: auto 700px;
    background-position: 50% 0;
  }
}

.start-scree__slider-wrapper {
  padding-top: 90px;

  @media screen and (max-width: @small) {
    padding-top: 40px;
  }
}

.start-screen__slide {
  outline: none;
}

.start-screen__title-img {
  width: 274px;
  height: 71px;
  margin-bottom: 24px;
}

.start-screen__slogan {
  font-size: 28px;
  line-height: 35px;
  display: block;
  width: 100%;
  max-width: 400px;
  margin-bottom: 24px;
}

.start-screen__content {
  width: 100%;
  max-width: 400px;
  margin-bottom: 24px;
}

.start-screen-contact-us__btn {
  margin-bottom: 82px;

  @media screen and (max-width: @small) {
    margin-bottom: 32px;
  }
}

.start-screen__info {
  padding: 60px;
  display: flex;
  justify-content: space-between;
  background: linear-gradient(87.71deg, #18449E 0%, #2D65BC 100%);
  border-radius: 4px;

  @media screen and (max-width: @large) {
    padding: 30px;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.start-screen__info-wrapper {
  width: 100%;
  max-width: 48%;

  @media screen and (max-width: @small) {
    max-width: 100%;
  }
}

.start-screen__analyzes-slogan {
  display: block;
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  color: @white;
  margin-bottom: 12px;
  padding-left: 30px;
  position: relative;

  span {
    color: @red;
  }


  .start-screen__icon {
    width: 25px;
    height: 25px;
    position: absolute;
    top: 0;
    left: 0;
    fill: @white;
  }
}

.start-screen__analyzes-txt {
  color: @white;
  margin-bottom: 24px;
  padding-left: 30px;
}

.start-screen__telephone {
  margin-bottom: 10px;
  padding-left: 30px;
  position: relative;

  .start-screen__icon {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    fill: @white;
  }
}

.start-screen__mail {
  margin-bottom: 0;
  padding-left: 30px;
  position: relative;

  .start-screen__icon {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    fill: @white;
  }
}

.start-screen__telephone-link,
.start-screen__mail-link {
  font-size: 16px;
  font-weight: bold;
  line-height: 23px;
  color: @white;
}

.start-screen__work-time-slogan {
  display: block;
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  color: @white;
  margin-bottom: 12px;
  padding-left: 30px;
  position: relative;

  .start-screen__icon {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    fill: @white;
  }
}

.start-screen__work-time-txt {
  color: @white;
  margin-bottom: 24px;
  padding-left: 30px;
}

.start-screen__work-time-table {
  width: calc(100% - 65px);
  margin-left: 25px;

  tr {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid @white;

    &:last-child {
      padding-top: 10px;
    }

    td {
      color: @white;

      &:last-child {
        font-weight: bold;
      }
    }
  }
}

.slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;

  li {
    width: 25px;
    height: 25px;
    margin: 0 5px;

    button {
      display: block;
      height: 6px;
      width: 6px;
      background-color: @blue;
      font-size: 0;
      border: none;
      padding: 0;
      border-radius: 3px;
      opacity: 0.6;
      margin: 0 auto;
    }
  }

  .slick-active {
    button {
      width: 22px;
      opacity: 1;
      margin: 0;
    }
  }
}
