.header {
  background-color: @white;
}

.header__wrapper {
  max-width: 1440px;
  padding: 18px 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;

  @media screen and (max-width: @large) {
    padding: 18px 20px;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    padding-bottom: 40px;
  }
}

.header__logo {
  transition: all ease-in-out 0.2s;

  &:hover {
    transform: scale(1.1);
  }
}

.header__nav {
  margin-left: auto;
  margin-right: 40px;

  @media screen and (max-width: @small) {
    display: none;
    margin-left: 0;
    margin-right: 0;
    width: 100%;
  }
}

.header__nav-list {
  width: 100%;
  max-width: 300px;
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @small) {
    flex-direction: column;
    max-width: 100%;
  }
}

.header__nav-list-item {
  margin-right: 40px;

  @media screen and (max-width: @small) {
    margin-right: 0;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 12px;
    border-bottom: 1px solid @black;
  }

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.header__nav-list-item--active {
  border-bottom: 1px solid @red;

  @media screen and (max-width: @small) {
    border-bottom: 1px solid @black;

    .header__nav-list-link {
      color: @red;
    }
  }
}

.header__nav-list-link {
  font-size: 16px;
  line-height: 24px;
  color: @black;
  border-bottom: 1px solid transparent;
  transition: all ease-in-out 0.2s;

  &:hover {
    border-bottom: 1px solid @red;
  }

  &:active {
    opacity: 0.8;
  }
}

.header-contact-us__btn {
  margin-left: 40px;
}

.header__mobile-btn {
  display: none;
  height: 25px;
  width: 30px;
  position: relative;
  animation: scale 0.3s;

  @media screen and (max-width: @small) {
    display: block;
    position: absolute;
    top: 25px;
    right: 20px;
  }

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: @red;
    position: absolute;
    bottom: 0;
    left: 0;
    border-radius: 1px;
  }

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: @red;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: block;
    width: 80%;
    height: 3px;
    background-color: @red;
    position: absolute;
    top: 11px;
    right: 0;
    border-radius: 1px;
  }
}

.header__mobile-btn--close {
  animation: 0.4s scale2;

  &::before {
    content: "";
    display: block;
    width: 28px;
    height: 3px;
    background-color: @red;
    position: absolute;
    bottom: 11px;
    left: 0;
    transform: rotate(135deg);
    border-radius: 1px;
  }

  &::after {
    content: "";
    display: block;
    width: 28px;
    height: 3px;
    background-color: @red;
    position: absolute;
    top: 11px;
    left: 0;
    transform: rotate(45deg);
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: none;
  }
}

.header__contact-us-btn {
  @media screen and (max-width: @small) {
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    width: 80%;
    text-align: center;
  }
}

@keyframes scale {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes scale2 {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
