    padding-top: 10px;
    padding-bottom: 12px;
    border-bottom: 1px solid #353535;
  }
}
.header__nav-list-item:last-child {
  margin-right: 0;
}
.header__nav-list-item:hover {
  opacity: 0.8;
}
.header__nav-list-item:active {
  opacity: 0.6;
}
.header__nav-list-item--active {
  border-bottom: 1px solid #E30613;
}
@media screen and (max-width: 576px) {
  .header__nav-list-item--active {
    border-bottom: 1px solid #353535;
  }
  .header__nav-list-item--active .header__nav-list-link {
    color: #E30613;
  }
}
.header__nav-list-link {
  font-size: 16px;
  line-height: 24px;
  color: #353535;
  border-bottom: 1px solid transparent;
  transition: all ease-in-out 0.2s;
}
.header__nav-list-link:hover {
  border-bottom: 1px solid #E30613;
}
.header__nav-list-link:active {
  opacity: 0.8;
}
.header-contact-us__btn {
  margin-left: 40px;
}
.header__mobile-btn {
  display: none;
  height: 25px;
  width: 30px;
  position: relative;
  animation: scale 0.3s;
}
@media screen and (max-width: 576px) {
  .header__mobile-btn {
    display: block;
    position: absolute;
    top: 25px;
    right: 20px;
  }
}
.header__mobile-btn::before {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #E30613;
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 1px;
}
.header__mobile-btn::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #E30613;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 1px;
}
.header__mobile-btn .header__mobile-btn-decor {
  display: block;
  width: 80%;
  height: 3px;
  background-color: #E30613;
  position: absolute;
  top: 11px;
  right: 0;
  border-radius: 1px;
}
.header__mobile-btn--close {
  animation: 0.4s scale2;
}
.header__mobile-btn--close::before {
  content: "";
  display: block;
  width: 28px;
  height: 3px;
  background-color: #E30613;
  position: absolute;
  bottom: 11px;
  left: 0;
  transform: rotate(135deg);
  border-radius: 1px;
}
.header__mobile-btn--close::after {
  content: "";
  display: block;
  width: 28px;
  height: 3px;
  background-color: #E30613;
  position: absolute;
  top: 11px;
  left: 0;
  transform: rotate(45deg);
  border-radius: 1px;
}
.header__mobile-btn--close .header__mobile-btn-decor {
  display: none;
}
@media screen and (max-width: 576px) {
  .header__contact-us-btn {
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    width: 80%;
    text-align: center;
  }
}
@keyframes scale {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes scale2 {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
